.modal-wrapper {
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: rgba(0, 0, 0, .7);
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.backdrop {
  width: 14px;
  height: 14px;
  cursor: pointer;
  opacity: .7;
  transition: transform .3s, background-color .3s, opacity .3s;
  position: absolute;
  top: 16px;
  right: 16px;
}

.backdrop:hover {
  opacity: 1;
}

.backdrop:before, .backdrop:after {
  content: " ";
  height: 14px;
  width: 2px;
  background-color: #000;
  transition: background-color .3s;
  position: absolute;
  left: 5px;
}

.backdrop:before {
  transform: rotate(45deg);
}

.backdrop:after {
  transform: rotate(-45deg);
}

.backdrop:active {
  background-color: #ff6b01;
  transform: rotate(180deg);
}

.backdrop:active:before, .backdrop:active:after {
  background-color: #fff;
}

