.trailer-modal-wrapper {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(0, 0, 0, .9);
  justify-content: center;
  align-items: center;
  animation: fadeIn .3s ease-out;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.trailer-modal-wrapper.open {
  display: flex;
}

.trailer-modal-content {
  width: 500px;
  height: 300px;
  background: #1a1a1a;
  border-radius: 12px;
  padding: 10px;
  animation: scaleIn .3s ease-out;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 20px rgba(0, 0, 0, .5);
}

.trailer-modal-content iframe {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 8px;
}

.trailer-backdrop {
  width: 100%;
  height: 100%;
  z-index: -1;
  background: none;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes scaleIn {
  from {
    transform: scale(.8);
  }

  to {
    transform: scale(1);
  }
}

